@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital@0;1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital@0;1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital@0;1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  height: 100%;
  font-family: "Playfair Display", serif;
}

body {
  height: 100%;
}

#root {
  height: 100%;
}

header {
  height: 100%;
}
.grid-container {
  display: grid;
  grid-template-columns: 0.7fr 4fr;
  height: 100%;
}

.header-title {
  font-family: "Playfair Display", serif;
  font-weight: 200;
}

.main-nav {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: top;
          align-items: top;
  width: 100%;
}

.main-nav__items {
  font-family: "Playfair Display", serif;
  list-style-type: none;
  height: 30%;
  text-align: center;
  width: 100%;
  margin: 40px 0;
  font-weight: 400;
}

.main-nav a {
  text-decoration: none;
  width: 100%;
  margin-right: 30px;
}

.main-nav__item a {
  color: #333;
  transition: font-weight 0.2s;
  width: 100%;
  transition: color 0.5s;
}

.main-nav__item a:hover,
.main-nav__item a:active {
  color: #67bab2;
}

.main-nav__item {
  padding-bottom: 40px;
  padding-right: 20px;
  font-size: 20px;
  width: 100%;
}

#logo-title {
  margin: 20px;
  font-size: 30px;
  color: black;
  text-align: center;
}

#quote {
  font-size: 20px;
  font-style: italic;
  font-weight: 300;
  text-align: center;
}

img {
  padding: 5px;
  margin-right: 30px;
}

.social-link {
  margin-right: 30px;
  height: 40px;
}

.social-link a {
  margin: auto;
}

.profile-pic img {
  border-radius: 50%;
  border: 1px solid black;
}

@media (max-width: 1000px) {
  .profile-pic {
    display: none;
  }
}

.profile-pic {
  width: 165px;
  margin: 10px auto;
}

.contact-info {
  text-align: center;
  font-style: italic;
  font-weight: 300;
  color: darkslategrey;
  font-size: 16px;
  margin: 0 8px;
}

.welcome {
  font-family: "Playfair Display", serif;
  color: white;
  font-size: 26px;
  font-weight: 400;
  width: 40%;
  text-align: justify;
  margin: 5% auto;
  padding: 12px 18px;
  border-radius: 8px;
}

.welcome a {
  text-decoration: none;
  color: #81d8d0;
  padding: 5px;
  margin: 5px;
  font-size: 23px;
  font-weight: bold;
  cursor: pointer;
}
.welcome h1 {
  color: #81d8d0;
  -webkit-animation-name: fade;
          animation-name: fade;
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.welcome p {
  -webkit-animation-name: fade;
          animation-name: fade;
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.welcome a:hover,
.welcome a:active {
  background-color: transparent;
  color: #81d8d0;
}

@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.about {
  width: 90%;
  margin: auto;
}

.about-text {
  width: 50%;
  text-align: justify;
}

#section-title {
  color: white;
  text-align: right;
  width: 90%;
  margin: 30px auto;
}

.title__message {
  text-align: center;
  z-index: 100;
  color: white;
  overflow: hidden;
  margin: 20px 30px;
  text-align: left;
  font-size: 90px;
  font-family: "Playfair Display", serif;
}
.typed-text {
  font-family: "Playfair Display, serif";
  font-style: italic;
  color: #81d8d0;
}

.cursor {
  -webkit-animation: blink 1s infinite;
          animation: blink 1s infinite;
  width: 1px;
  background-color: #81d8d0;
  margin-left: 0.1rem;
  font-weight: 100;
  font-size: 1em;
  font-family: "Playfair Display", serif;
}

@-webkit-keyframes blink {
  0% {
    background-color: #81d8d0;
  }
  40% {
    background-color: #81d8d0;
  }
  50% {
    background-color: transparent;
  }
  90% {
    background-color: transparent;
  }
  100% {
    background-color: #81d8d0;
  }
}

@keyframes blink {
  0% {
    background-color: #81d8d0;
  }
  40% {
    background-color: #81d8d0;
  }
  50% {
    background-color: transparent;
  }
  90% {
    background-color: transparent;
  }
  100% {
    background-color: #81d8d0;
  }
}

.cursor.typing {
  -webkit-animation: none;
          animation: none;
}

.projects__container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin: 0 60px;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.project__container {
  -webkit-flex: 40% 1;
          flex: 40% 1;
  margin: 10px;
  padding: 10px;
  height: auto;
  box-shadow: 2px 2px 11px 2px rgba(0, 0, 0, 0.2);
  min-width: 350px;
  text-align: justify;
}

.project__container a {
  color: #67bab2;
  font-weight: bold;
  text-decoration: none;
  font-size: 20px;
  transition: color 0.5s;
}

.project__container a:hover,
.project__container a:active {
  color: black;
}

#title {
  display: block;
  width: 100%;
  color: #67bab2;
  margin-left: 10px;
}

.image__container {
  height: 50%;
  width: 100%;
  text-align: center;
}

img {
  max-width: 100%;
  max-height: 100%;
  margin: 0%;
}

.project-desc {
  font-weight: 250;
  color: #333;
}

